@import "~normalize.css";
@font-face {
  font-family: "Inter";
  src: url("https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700;800;900&display=swap") format("woff2");
  font-style: normal;
  font-display: swap;
}
*, *:before, *:after {
  box-sizing: border-box;
}

html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-family: Inter, sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #212121;
}

img {
  max-width: 100%;
  height: auto;
}

h2 {
  font-size: 30px;
  margin: 0 0 30px;
}

p {
  margin: 0;
}

.otgs-development-site-front-end {
  display: none;
}

.container {
  width: 1100px;
  margin: auto;
}
@media screen and (max-width: 767.98px) {
  .container {
    padding: 0 15px;
  }
}
@media screen and (max-width: 1199.98px) {
  .container {
    width: 100%;
  }
}

.content h1, .content h2, .content h3, .content h4, .content h5, .content h6 {
  font-weight: 700;
  margin: 1.5em 0 1em;
}
.content h1 {
  font-size: 28px;
}
.content h2 {
  font-size: 24px;
}
.content h3 {
  font-size: 22px;
}
.content h4 {
  font-size: 18px;
}
.content h5 {
  font-size: 16px;
}
.content h6 {
  font-size: 14px;
}
.content p {
  margin-bottom: 1em;
}
.content p:last-child {
  margin-bottom: 0;
}
.content a {
  text-decoration: underline;
  color: #212121;
}
.header {
  padding: 20px 0;
}
.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (max-width: 767.98px) {
  .header .container {
    justify-content: flex-start;
  }
}
.header__logo img {
  max-width: 160px;
}
@media screen and (max-width: 575.98px) {
  .header__logo img {
    max-width: 100px;
  }
}
.header__menu {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
}
@media screen and (max-width: 767.98px) {
  .header__menu {
    position: absolute;
    z-index: 500;
    background: #fff;
    top: 0;
    right: 0;
    width: 100%;
    max-width: 340px;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transform: translateX(calc(100% + 10px));
    transition: 0.3s;
  }
  .header__menu_active {
    transform: translateX(0);
  }
}
.header__menu li {
  margin: 0 15px;
  list-style: none;
}
@media screen and (max-width: 767.98px) {
  .header__menu li {
    margin: 15px 0;
  }
}
.header__menu a {
  color: #212121;
  font-weight: 500;
  text-decoration: none;
  transition: 0.3s;
}
.header__menu a:hover {
  color: #FF8900;
}
.header__menu a.current {
  color: #FF8900;
  font-weight: 700;
}
.header__button {
  text-decoration: none;
  color: #fff;
  background: #FF8900;
  border: 1px solid #FF8900;
  padding: 8px 15px;
  font-weight: 500;
  border-radius: 5px;
  transition: 0.3s;
  min-width: 130px;
  text-align: center;
}
.header__button:hover {
  background: #fff;
  color: #FF8900;
}
@media screen and (max-width: 767.98px) {
  .header__button {
    margin: 0 18px 0 auto;
  }
}
.header__burger {
  display: none;
  position: relative;
  width: 20px;
  height: 14px;
  cursor: pointer;
  z-index: 700;
}
@media screen and (max-width: 767.98px) {
  .header__burger {
    display: block;
  }
}
.header__burger span {
  position: absolute;
  left: 50%;
  top: 50%;
  display: block;
  width: 20px;
  height: 2px;
  background-color: #212121;
  transition: 0.3s;
}
.header__burger span:nth-child(1) {
  transform: translate(-50%, calc(-50% - 7px));
}
.header__burger span:nth-child(2) {
  transform: translate(-50%, -50%);
}
.header__burger span:nth-child(3) {
  transform: translate(-50%, calc(-50% + 7px));
}
.header__burger_active span:nth-child(1) {
  transform: translate(-50%, -50%) rotate(45deg);
}
.header__burger_active span:nth-child(2) {
  opacity: 0;
}
.header__burger_active span:nth-child(3) {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.language-switcher {
  list-style: none;
  padding: 0;
  margin: 0;
}

.language-switcher li {
  display: inline-block;
  margin-right: 10px;
}

.language-switcher li a {
  text-decoration: none;
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.language-switcher li.active-language a {
  background-color: #007bff;
  color: #fff;
  border-color: #007bff;
}

.footer {
  padding: 20px 0;
  background: #f7fbff;
}
.footer__copyright {
  font-size: 12px;
}

.banner {
  padding-bottom: 2.5rem;
  padding-top: 3.75rem;
  background-color: #ffffff;
}
.banner__left {
  width: 50%;
}
@media screen and (max-width: 767.98px) {
  .banner__left {
    width: 100%;
  }
}
.banner__right {
  width: 50%;
}
@media screen and (max-width: 767.98px) {
  .banner__right {
    display: none;
  }
}
.banner .container {
  display: flex;
  flex-direction: row;
}
@media screen and (max-width: 767.98px) {
  .banner .container {
    flex-direction: column;
  }
}
@media screen and (max-width: 575.98px) {
  .banner {
    padding: 50px 0;
  }
}
.banner h1 {
  font-size: 48px;
  line-height: 60px;
  font-weight: 900;
  margin-top: 1.5rem;
  max-width: 550px;
}
@media screen and (max-width: 575.98px) {
  .banner h1 {
    font-size: 1.5rem;
    font-weight: 900;
    line-height: 2rem;
  }
}
.banner p {
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: rgb(136, 136, 136);
  text-transform: uppercase;
}
@media screen and (max-width: 575.98px) {
  .banner p {
    font-size: 18px;
  }
}
.banner__button {
  margin-top: 1.25rem;
}
.banner__button a {
  background-color: rgb(103, 32, 255);
  border-color: rgb(103, 32, 255);
  border-radius: 8px;
  border-width: 1px;
  box-shadow: 0 0 rgba(0, 0, 0, 0), 0 0 rgba(0, 0, 0, 0), 0px 4px 12px rgba(103, 32, 255, 0.25);
  color: rgb(255, 255, 255);
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 1px;
  line-height: 20px;
  padding: 1.25rem 1.5rem;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
}
.banner__button a:hover {
  --tw-border-opacity:1;
  --tw-bg-opacity: 1;
  --tw-text-opacity: 1;
  --tw-shadow: 0px 12px 24px rgba(103,32,255,.25);
  --tw-shadow-colored: 0px 12px 24px var(--tw-shadow-color);
  background-color: rgb(141 69 246/var(--tw-bg-opacity));
  border-color: rgb(141 69 246/var(--tw-border-opacity));
  box-shadow: var(--tw-ring-offset-shadow, 0 0 rgba(0, 0, 0, 0)), var(--tw-ring-shadow, 0 0 rgba(0, 0, 0, 0)), var(--tw-shadow);
  color: rgb(255 255 255/var(--tw-text-opacity));
}

.screen {
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}
.screen h2 {
  text-align: center;
  font-size: 34px;
  font-weight: 700;
  line-height: 1.5;
}
@media screen and (max-width: 767.98px) {
  .screen h2 {
    font-size: 1.5rem;
  }
}

.price {
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}
.price .container {
  display: flex;
  flex-direction: row;
}
@media screen and (max-width: 767.98px) {
  .price .container {
    flex-direction: column;
  }
}
.price__right {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.price__right-desc {
  font-size: 18px;
  line-height: 140%;
  margin-bottom: 16px;
}
.price__right h2 {
  font-size: 34px;
  line-height: 1.5;
  font-weight: 700;
}
@media screen and (max-width: 767.98px) {
  .price__right h2 {
    font-size: 1.5rem;
