}
.json .container {
  display: flex;
  flex-direction: row;
}
@media screen and (max-width: 575.98px) {
  .json .container {
    flex-direction: column;
  }
}
.json__right {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.json__right-desc {
  font-size: 18px;
  line-height: 140%;
  margin-bottom: 16px;
}
.json__right h2 {
  font-size: 34px;
  line-height: 1.5;
